   color: #dc3545;
   border: 1px solid #e9ecef
}

.comment-form input[type=text],
.comment-form input[type=email],
.comment-form input[type=url],
.comment-form textarea {
   border: 1px solid #dddddd;
   border-radius: 2px;
   background-color: #efefef;
   display: block;
   padding: 10px;
}

.menu-item:hover>.dropdown-menu {
   display: block;
}

@media (max-width: 768px) {
   .navbar-collapse {
      position: fixed;
      top: 0px;
      left: -70%;
      width: 300px;
      max-width: 70%;
      height: 100vh;
      background-color: #fff;
      padding: 20px;
      z-index: 99;
      overflow-y: auto;
      transition: all ease 200ms
   }

   .navbar-collapse a {
      color: #333 !important;
   }

   .logged-in .navbar-collapse {
      padding-top: 70px;
   }

   .logged-in .navbar-collapse.show {
      padding-top: 70px;
   }

   .navbar-collapse.show {
      left: 0%;
      transition: all ease 200ms;
      box-shadow: 0 .125rem .25rem rgba(0, 0, 0, .075) !important;
   }

   .dropdown-menu {
      box-shadow: 0 0 0 !important;
      padding: 0;
   }

   .dropdown-item {
      padding: .25rem .9rem;
      white-space: inherit;
   }

   .navbar-collapse .dropdown-menu {
      background-color: #fff !important;
   }
}


#comments .comments-title {
   font-size: 20px;
   margin-bottom: 1.5rem;
}

#comments .comment-list {
   list-style: none;
   padding-left: 0px;
}

#comments .comment-body {
   clear: both;
   margin-bottom: 1.5rem;
}

#comments .comment-meta img {
   float: left;
   margin-right: 10px;
   border-radius: 5px;
}

.comment-list li .fn,
.comment-list li .cfn {
   text-transform: uppercase;
   display: inline-block;
}

.comment-list li .says {
   display: none;
}

#comments .reply {
   margin: -50px 0 0 10px;
   padding: 5px 10px;
   opacity: 0.5;
}

#comments .reply>a:before {
   content: '\f112';
   font-family: 'fontawesome';
   position: relative;
   margin-right: 5px;
   font-size: 70%;
}

#comments footer.comment-meta .comment-metadata {
   font-size: 80%;
}

#comments .comment-content {
   padding: 10px 20px 45px;
   margin: 10px 0px 15px;
   font-size: 15px;
   border: 1px solid #f1f1f1;
   border-radius: 5px;
   line-height: 1.5em !important;
}

.block-header {
   border: none;
   border-bottom: solid;
}

.breadcrumbs,
.breadcrumbs a,
.breadcrumbs a:link,
.breadcrumbs a:visited {
   color: var(--secondary);
   font-size: .95em;
}

blockquote {
   font-style: italic;
   padding: 2em;
   position: relative;
   background: #f3f3f3;
   margin: 1rem 0;
}

blockquote:before {
   display: block;
   content: "\f10d";
   font-size: 1.3rem;
   position: absolute;
   left: -10px;
   top: -10px;
   color: #7a7a7a;
   font-family: fontawesome;
}

.entry-meta,
.entry-footer {
   font-size: 0.9em;
}

.widget_calendar {
   width: 100%;
