#wp-calendar {
   width: 100%;
}

#wp-calendar caption {
   text-align: right;
   color: #333;
   font-size: 12px;
   margin-top: 10px;
}

#wp-calendar thead {
   font-size: 10px;
}

#wp-calendar thead th {
   padding: 5px 2px;
   text-align: center;
   font-size: 16px;
   color: var(--primary);
   border: 0px;
}

#wp-calendar tbody {
   color: #aaa;
}

#wp-calendar #today {
   color: var(--primary);
   font-weight: bold;
}

#wp-calendar tbody td {
   background: #f5f5f5;
   border: 1px solid #fff;
   text-align: center;
   padding: 10px 2px;
}

#wp-calendar tbody td:hover {
   background: #fff;
}

#wp-calendar tbody .pad {
   background: none;
}

#wp-calendar tfoot #next {
   font-size: 10px;
   text-transform: uppercase;
   text-align: right;
}

#wp-calendar tfoot #prev {
   font-size: 10px;
   text-transform: uppercase;
   padding-top: 10px;
}

.whatsapp-float,
.to-top-float {
   bottom: 10px;
   z-index: 10;
}

.wa-left {
   left: 10px;
}

.to-top-float,
.wa-right {
   right: 10px;
}

.to-top-float {
   display: none;
}

.wa-right {
   transition: all 200ms ease;
}

.wa-right.push-left {
   transition: all 200ms ease;
